"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Gamepad2, Clock, CheckCircle, Info, Trophy } from "lucide-react";
import {
  createRoom,
  joinRoom,
  onLeaderboardUpdate,
  closeRoom,
  startGame,
  onRoomStatusUpdate,
  getRoomStatus,
  endGame
} from "@/utils/firebase";
import CardRulesPopup from "./card-rules-popup";
import GamePlay from "./game-play";
import { Leaderboard } from "./leaderboard";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";

export function Game() {
  const [roomCode, setRoomCode] = useState("");
  const [playerName, setPlayerName] = useState("");
  const [isAdmin, setIsAdmin] = useState(false);
  const [joinedPlayers, setJoinedPlayers] = useState<{ id?: string; name: string; score: number }[]>([]);
  const [showCardRules, setShowCardRules] = useState(false);
  const [playerId, setPlayerId] = useState<string | null>(null);
  const [gameStarted, setGameStarted] = useState(false);
  const [roomClosed, setRoomClosed] = useState(false);
  const [gameEnded, setGameEnded] = useState(false);

  // check admin
  useEffect(() => {
    const adminKey = localStorage.getItem("ADMIN_KEY");
    if (adminKey === "BO_MAY_LA_ADMIN") setIsAdmin(true);
  }, []);

  // realtime listeners
  useEffect(() => {
    if (!roomCode) return;

    const unsubscribeLeaderboard = onLeaderboardUpdate(roomCode, (players) => setJoinedPlayers(players));
    const unsubscribeRoomStatus = onRoomStatusUpdate(roomCode, ({ status, gameStarted }) => {
      setRoomClosed(status === "closed");
      setGameStarted(gameStarted);
      setGameEnded(status === "endGame");
    });

    return () => {
      unsubscribeLeaderboard?.();
      unsubscribeRoomStatus?.();
    };
  }, [roomCode]);

  // --- Admin actions ---
  const handleCreateRoom = async () => {
    const { roomCode } = await createRoom();
    setRoomCode(roomCode);
    setGameStarted(false);
    setRoomClosed(false);
    setGameEnded(false);
    onLeaderboardUpdate(roomCode, (players) => setJoinedPlayers(players));
  };

  const handleStartGame = async () => {
    if (!roomCode) return;
    await startGame(roomCode);
  };

  const handleCloseRoom = async () => {
    if (!roomCode) return;
    await closeRoom(roomCode);
    setRoomClosed(true);
  };

  const handleEndGame = async () => {
    if (!roomCode) return;
    await endGame(roomCode);
    setGameStarted(false);
    setGameEnded(true);
  };

  // --- Player actions ---
  const handleJoinRoom = async () => {
    if (!roomCode || !playerName.trim()) return;

    const status = await getRoomStatus(roomCode);
    if (!status || status.status === "closed") {
      alert("Phng ny  b ng, khng th tham gia.");
      return;
    }

    const id = await joinRoom(roomCode, playerName.trim());
    setPlayerId(id);

    onLeaderboardUpdate(roomCode, (players) => setJoinedPlayers(players));
    onRoomStatusUpdate(roomCode, ({ status, gameStarted }) => {
      setRoomClosed(status === "closed");
      setGameStarted(gameStarted);
      setGameEnded(status === "endGame");
    });
  };

  const resetGameState = () => {
    setRoomCode("");
    setPlayerName("");
    setPlayerId(null);
    setJoinedPlayers([]);
    setGameStarted(false);
    setRoomClosed(false);
    setGameEnded(false);
  };

  return (
    <div className="mx-auto max-w-2xl space-y-8">
      {/* End Game Popup cho ngi chi */}
      {!isAdmin && gameEnded && (
  <Dialog open={gameEnded} onOpenChange={(open) => {
    if (!open) {
      resetGameState();
    }
  }}>
    <DialogContent className="max-w-md">
      <DialogHeader>
        <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-yellow-500/10">
          <Trophy className="h-8 w-8 text-yellow-500" />
        </div>
        <DialogTitle className="text-center text-2xl">
          Tr chi  kt thc!
        </DialogTitle>
        <DialogDescription className="text-center">
          Cm n bn  tham gia. Di y l bng xp hng cui cng.
        </DialogDescription>
      </DialogHeader>

      {/* === SCROLL AREA === */}
      <div className="max-h-[60vh] overflow-y-auto pr-2">
        <Leaderboard
          players={joinedPlayers}
          currentPlayerId={playerId}
        />
      </div>
    </DialogContent>
  </Dialog>
)}


      {isAdmin ? (
        <>
          {/* === GAME  KT THC === */}
          {gameEnded && (
            <>
              <p className="text-center text-xl font-bold text-yellow-600">
                Tr chi  kt thc
              </p>

              <Leaderboard players={joinedPlayers} />

              <div className="flex justify-center gap-2 mt-4">
                <Button onClick={handleCreateRoom}>To phng mi</Button>
              </div>
            </>
          )}

          {/* === GAME ANG CHI === */}
          {gameStarted && !gameEnded && (
            <>
              <div className="flex justify-center gap-2">
                <Button onClick={handleEndGame} variant="destructive">
                  Kt thc tr chi
                </Button>
              </div>

              <p className="mt-2 text-center text-lg font-semibold">
                M phng: {roomCode}
              </p>
              <p className="text-center text-sm text-muted-foreground">
                S ngi tham gia: <strong>{joinedPlayers.length}</strong>
              </p>

              <Leaderboard players={joinedPlayers} />
            </>
          )}

          {/* === CHA BT U === */}
          {!gameStarted && !gameEnded && (
            <Card className="border-2 border-primary/20 shadow-xl">
              <CardHeader className="text-center">
                <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-primary/10">
                  <Gamepad2 className="h-8 w-8 text-primary" />
                </div>
                <CardTitle className="text-2xl">Giao din Admin</CardTitle>
                <CardDescription>To phng v theo di ngi chi tham gia</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4 text-center">
                <div className="flex justify-center gap-2">
                  <Button onClick={handleCreateRoom}>To phng mi</Button>
                  {roomCode && !roomClosed && <Button onClick={handleStartGame}>Bt u</Button>}
                  {roomCode && !roomClosed && (
                    <Button onClick={handleCloseRoom} variant="destructive">
                      ng phng
                    </Button>
                  )}
                </div>
                {roomCode && <p className="mt-2 text-lg font-semibold">M phng: {roomCode}</p>}
                {joinedPlayers.length > 0 && (
                  <div className="mt-4 space-y-2 border p-2 rounded">
                    <p className="font-medium mb-2">Danh sch ngi tham gia:</p>
                    <div className="flex flex-wrap gap-2">
                      {joinedPlayers.map((p, idx) => (
                        <span
                          key={idx}
                          className="inline-flex items-center gap-1 bg-primary/20 dark:bg-primary/30 text-primary px-3 py-1 rounded-full text-sm font-medium"
                        >
                          {p.name}
                          {gameStarted && <span className="ml-1 text-xs font-normal">({p.score} im)</span>}
                        </span>
                      ))}
                    </div>
                  </div>
                )}
                {roomClosed && <p className="mt-2 text-red-600 font-semibold">Phng  b ng</p>}
              </CardContent>
            </Card>
          )}
        </>
      ) : (
        <>
          {/* === Player UI === */}
          {!playerId && (
            <Card className="border-2 border-primary/20 shadow-xl">
              <CardHeader className="text-center">
                <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-primary/10">
                  <Gamepad2 className="h-8 w-8 text-primary" />
                </div>
                <CardTitle className="text-2xl">Tham gia tr chi</CardTitle>
                <CardDescription>Nhp m phng  tham gia cng bn b</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex gap-3">
                  <Input placeholder="Tn ca bn" value={playerName} onChange={(e) => setPlayerName(e.target.value)} />
                  <Input
                    placeholder="Nhp m phng..."
                    value={roomCode}
                    onChange={(e) => setRoomCode(e.target.value.toUpperCase())}
                    className="text-center text-lg font-semibold tracking-widest"
                    maxLength={6}
                  />
                  <Button onClick={handleJoinRoom}>Tham gia</Button>
                </div>
              </CardContent>
            </Card>
          )}

          {playerId && (
            <>
              {gameStarted && !gameEnded ? (
                <GamePlay roomCode={roomCode} playerId={playerId} />
              ) : !gameEnded ? (
                <Card className="border-2 border-primary/20 shadow-xl">
                  <CardHeader>
                    <CardTitle>Phng: {roomCode}</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-2 max-h-64 overflow-y-auto border p-2 rounded">
                    {joinedPlayers.length > 0 && (
                      <div className="mt-4 space-y-2 border p-2 rounded">
                        <p className="font-medium mb-2">Danh sch ngi tham gia:</p>
                        <div className="flex flex-wrap gap-2">
                          {joinedPlayers.map((p, idx) => (
                            <span
                              key={idx}
                              className="inline-flex items-center gap-1 bg-primary/20 dark:bg-primary/30 text-primary px-3 py-1 rounded-full text-sm font-medium"
                            >
                              {p.name}
                              {gameStarted && <span className="ml-1 text-xs font-normal">({p.score} im)</span>}
                            </span>
                          ))}
                        </div>
                      </div>
                    )}
                    {(roomClosed || !gameStarted) && (
                      <p className={`mt-2 font-semibold ${roomClosed ? "text-red-600" : "text-sm text-muted-foreground"}`}>
                        {roomClosed ? "Phng  b ng" : "Ch admin bt u..."}
                      </p>
                    )}
                  </CardContent>
                </Card>
              ) : null}
            </>
          )}
        </>
      )}

      {/* === Lut chi === */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Info className="h-5 w-5 text-primary" />
            Lut chi
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Thi gian */}
          <div className="flex gap-3 rounded-lg bg-muted/50 p-4">
            <Clock className="h-5 w-5 shrink-0 text-primary" />
            <div>
              <p className="font-medium">Thi gian</p>
              <p className="text-sm text-muted-foreground">15 giy cho mi cu hi</p>
              <p className="text-sm text-muted-foreground">
                D kin tr chi s din ra trong 8 pht, tr chi s kt thc khi admin ng phng
              </p>
            </div>
          </div>

          {/* Ni dung tr li */}
          <div className="flex gap-3">
            <CheckCircle className="h-5 w-5 shrink-0 text-emerald-500" />
            <p className="text-sm">Tr li cc cu hi v lch s Vit Nam t i hi VIII n nay</p>
          </div>

          {/* C ch bc bi */}
          <div className="flex gap-3">
            <CheckCircle className="h-5 w-5 shrink-0 text-emerald-500" />
            <div className="text-sm">
              C ch bc bi:
              <ul className="list-disc ml-5">
                <li>Mi ln tr li ng, bn c im da trn thi gian tr li nhanh/chm.</li>
                <li>Sau , bn c bc <strong>2 l card</strong> t b bi (c th b qua nu khng mun bc thm).</li>
                <li>Mi l card s nh hng trc tip n im s ca bn hoc i th (nhn im, cng/tr im, cp im, mt ton b)</li>
                <li>Nu tr li sai, bn khng c im v c th chuyn sang cu hi tip theo.</li>
              </ul>
            </div>
          </div>

          <Button className="mt-2" onClick={() => setShowCardRules(true)}>
            Xem lut cc loi card
          </Button>
        </CardContent>
      </Card>

      <CardRulesPopup open={showCardRules} onClose={() => setShowCardRules(false)} />
    </div>
  );
}